.wrapper {
  max-width: 1140px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1140px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.debug > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(8.33333% - 30px);
  height: inherit;
  outline: 1px #ff0 solid;
  background: rgba(0, 0, 0, 0.1);
}

.header {
  background: url(../img/img-togas-lp/02-header.jpg) no-repeat center top;
  background-size: cover;
  height: 100%;
  padding-top: 50px;
  padding-bottom: 300px;
  color: #fff;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
}

.justify-content-center {
  justify-content: center;
}

.logo {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(16.66667% - 30px);
  display: block;
  width: 147px;
}

.hamburger {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(8.33333% - 30px);
  width: 24px;
  cursor: pointer;
}

.hamburger span {
  background: #fff;
  display: block;
  height: 2px;
  margin-bottom: 5px;
}

.offer {
  margin-top: 170px;
  text-align: center;
}

.offer__title {
  font-size: 52px;
  font-weight: 300;
  color: #fff;
}

.offer__form {
  display: flex;
  margin-top: 24px;
}

.offer__form input {
  background: #fff;
  border: 0;
  width: 328px;
  display: block;
  padding-left: 15px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 16px;
}

.offer__form button {
  background: #fd4f32;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  width: 124px;
  padding: 10px 0;
  cursor: pointer;
  display: block;
  border: none;
}

@media screen and (max-width: 1140px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }

  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 960px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }

  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .debug > div {
    padding-left: 20px;
    padding-right: 20px;
  }

  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 540px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .debug > div {
    padding-left: 15px;
    padding-right: 15px;
  }

  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .offer__title {
    font-size: 40px;
  }
}

@media screen and (max-width: 400px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  .debug > div {
    padding-left: 5px;
    padding-right: 5px;
  }

  .debug > div > div {
    margin-left: -5px;
    margin-right: -5px;
  }

  .debug > div > div > div {
    margin-left: 5px;
    margin-right: 5px;
  }

  .debug > div > div > div {
    width: calc(8.33333% - 10px);
  }

  .container {
    padding-left: 5px;
    padding-right: 5px;
  }

  .row {
    margin-left: -5px;
    margin-right: -5px;
  }

  .logo {
    margin-left: 5px;
    margin-right: 5px;
  }

  .hamburger {
    margin-left: 5px;
    margin-right: 5px;
  }

  .offer__title {
    font-size: 25px;
  }

  .offer__form {
    flex-direction: column;
  }

  .offer__form input {
    width: auto;
  }

  .offer__form button {
    width: auto;
  }
}